@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
body {
  margin: 0;
  /* override bootstrap font family */
  font-family: "Montserrat", sans-serif !important; 
}

.logo-title-layout {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.logo-title-font {
  font-weight: 700;
  font-size: 48px;
  color: #5B3080;
  margin: 0;
}

.top-bar-layout {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav-bar-font li a, .nav-bar-font a {
  color: #171717;
  font-weight: 500;
  font-size: 20px;
}

.home-join-font {
  font-weight: 400;
  font-size: 25px;
  color: #171717;
}

.home-join-layout {
  margin: auto;
  width: 60%;
  text-align: center;
}

.home-join-button-font {
  font-weight: 700;
  font-size: 18px;
}

